  .test-canvas {
    display: block;
    position: relative;
    background-color: #fff;
    height: 270px;
    margin: 0 auto;
    font-family: "Droid Arabic Naskh", Georgia, Cambria, "Times New Roman", Times, serif;
    font-size: 24px;
    position: relative;
    overflow: hidden
  }
  
  .test-canvas .test-input {
    position: absolute;
    z-index: 5;
    opacity: 0;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: auto
  }
  
  canvas-scroller {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transition-property: transform;
    transition-duration: .2s
  }
  
  .canvas-line {
    display: block;
    white-space: nowrap;
    padding: 5px;
    border-bottom: 1px solid #eee;
    line-height: 44px
  }
  
  .canvas-line:last-child {
    border-bottom: none
  }
  
  .canvas-line .key-current,
  .canvas-line .key-error {
    background-color: #4db5e3;
    color: #fff;
    border-radius: 2px;
    display: inline-block;
    vertical-align: top;
    height: 44px;
    line-height: 44px
  }
  
  .canvas-line .key-error {
    background-color: inherit;
    border-bottom: none;
    color: #f44336
  }
  
  .canvas-line .tashkeel {
    color: #4db5e3;
    font-size: 1.5em;
    border-bottom: 2px solid #4db5e3
  }
  
  .canvas-line .tashkeel.error-key {
    color: #f44336
  }
  
  .canvas-line .key-success {
    color: #b7b7b7
  }
  
  .show-error-text {
    left: 50%;
    top: 0;
    position: absolute;
    display: block;
    padding: 15px;
    border-radius: 100%;
    background-color: #eee;
    color: #f44336;
    font-size: 1.2em;
    opacity: 0;
    animation-duration: 1s;
    animation-iteration-count: 1;
    animation-name: fadeup
  }
  
  @keyframes fadeup {
    from {
        transform: translateY(30px);
        opacity: .7
    }
  
    to {
        transform: translateY(-50px);
        opacity: 0
    }
  }
  
